@import url(https://fonts.googleapis.com/css?family=Annie+Use+Your+Telescope|Rajdhani|Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Annie+Use+Your+Telescope|Rajdhani|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer{background:#FFE1A4;text-align:center;grid-area:footer;width:100%;font-family:"Annie Use Your Telescope",cursive;font-style:normal;font-weight:normal;font-size:1.5em;text-align:center;text-transform:uppercase;color:#000000}

.card{cursor:pointer;display:flex;justify-content:center;align-items:center;padding:0.5em;width:5rem;height:5rem;overflow:hidden}.card::after,.card{border-radius:10%}.disabled{cursor:default;opacity:0.35}img{width:auto;height:auto;max-height:200%;max-width:200%}.flip-up{-webkit-animation:flip-vertical-right 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;animation:flip-vertical-right 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;border:1px solid #aaa;position:relative}.flip-up::after{-webkit-animation:flip-color 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;animation:flip-color 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;position:absolute;display:flex;justify-content:center;align-items:center;content:"";width:100%;height:100%}.flip-down{position:relative}.flip-down::after{-webkit-animation:flip-vertical-left 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;animation:flip-vertical-left 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;position:absolute;display:flex;justify-content:center;align-items:center;content:"";width:100%;height:100%}@-webkit-keyframes flip-vertical-right{0%{transform:rotateY(180deg)}100%{transform:rotateY(0)}}@keyframes flip-vertical-right{0%{transform:rotateY(180deg)}100%{transform:rotateY(0)}}@-webkit-keyframes flip-color{from{background-color:#2838c7bb}to{background-color:none}}@keyframes flip-color{from{background-color:#2838c7bb}to{background-color:none}}@-webkit-keyframes flip-vertical-left{0%{transform:rotateY(0)}100%{transform:rotateY(180deg);background-color:#2838c7bb}}@keyframes flip-vertical-left{0%{transform:rotateY(0)}100%{transform:rotateY(180deg);background-color:#2838c7bb}}

.game-over{display:flex;flex-direction:column;align-items:center;align-content:center;justify-content:center;height:50vh}.game-over h3{text-transform:uppercase}

main{display:grid;grid-gap:1rem;gap:1rem;grid-template:"header" auto "cards-section" 1fr "footer" auto;height:100vh;background-color:#3EE8D4}button,.btn{background-color:#7251FF !important}h1{font-family:"Rajdhani",sans-serif;font-style:normal;font-weight:normal;font-size:8vw;margin:0;text-align:center;text-transform:uppercase;color:#333333}header{grid-area:header;display:flex;justify-content:center}section.cards{margin:0 1rem;grid-area:cards-section;align-content:center;display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:auto 1.5fr;grid-template-areas:"aside article"}section.cards aside{grid-area:aside}section.cards article{justify-self:center;grid-area:article;display:grid;grid-gap:1rem;gap:1rem;grid-template:repeat(2, 1fr)/repeat(2, 1fr)}

main.login{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh;width:100vw}main.login>:first-child{flex:1 1}main.login section{display:flex;flex-direction:column;justify-content:center;align-items:center}main.login article{margin:4em}main.login article.invite{display:flex;justify-content:center;align-items:center}main.login article.user-name-input{display:grid;justify-items:center;align-items:center;grid-template:"text-input submit-button" 1fr/1fr auto;grid-gap:1em;gap:1em}main.login footer{align-self:flex-end;justify-self:flex-end}main.login .text-input{grid-area:text-input;width:100%}main.login .submit-button{grid-area:submit-button;width:100%;height:4em}main.login .btn.invite{width:12em;height:4em}#tooltip{background:#333;color:white;font-weight:bold;padding:4px 8px;font-size:13px;border-radius:4px}#arrow,#arrow::before{position:absolute;width:1em;height:1em;z-index:-1}#arrow::before{content:'';transform:rotate(45deg);background:#333}

